import React, { useState } from 'react';
import { Bar } from 'react-chartjs-2';
import { Chart as chartjs } from 'chart.js/auto';
import citiesTop10 from '../data/cities-top10-wlb.json';

function VacationDaysTop() {

  const [vacationDayData, setVacationDayData] = useState({
    labels: citiesTop10.map((data) => data.City),
    datasets: [
      {
        label: "Minimum Vacation Days",
        data: citiesTop10.map((data) => data["Minimum Vacations Offered (Days)"]),
        backgroundColor: 'rgba(255, 99, 132, 0.5)',
      }, 
      {
        label: "Vacations Days Taken",
        data: citiesTop10.map((data) => data["Vacations Taken (Days)"]),
        backgroundColor: 'rgba(53, 162, 235, 0.5)',
      }
    ]
  })

  const options = {
    plugins: {
      title: {
        display: true,
        text: 'Vacation Days in Top 10 Work-Life Balance Cities',
      },
    },
    responsive: true,
  };

  return (
    <Bar data={vacationDayData} options={options} />
  )
}

export default VacationDaysTop