import React, { useState } from 'react';
import { Bar } from 'react-chartjs-2';
import { Chart as chartjs } from 'chart.js/auto';
import citiesBottom10 from '../data/cities-bottom10-wlb.json';

function VacationDaysTop() {

  const [vacationDayData, setVacationDayData] = useState({
    labels: citiesBottom10.map((data) => data.City),
    datasets: [
      {
        label: "Minimum Vacation Days",
        data: citiesBottom10.map((data) => data["Minimum Vacations Offered (Days)"]),
        backgroundColor: '#FF8811',
      }, 
      {
        label: "Vacations Days Taken",
        data: citiesBottom10.map((data) => data["Vacations Taken (Days)"]),
        backgroundColor: '#7D70BA',
      }
    ]
  })

  const options = {
    plugins: {
      title: {
        display: true,
        text: 'Vacation Days in Bottom 10 Work-Life Balance Cities',
      },
    },
    responsive: true,
  };

  return (
    <Bar data={vacationDayData} options={options} />
  )
}

export default VacationDaysTop